import React from 'react';
import {getCategories, createGig, getGig, updateGig } from '../../../http/gig-calls';
import {switchLoader,alertBox} from '../../../commonRedux/';
import Button from '../../../components/Button';
import A from '../../../components/A';
import Spinner from '../../../components/Spinner';
require ("./styles.scss");
let coins = require('./coins.json');

class EditGig extends React.Component {
  constructor(props) {
    super(props);
      this.state = {
        id:null,
        level: 1,
        dragclass: '',
        gig: null,
        parent: "",
        files: [],
        features: [{feature: '', standard:0, premium: 1}],
        extras: [{feature: '', value: '', amount: ''}],
        level1: [],
        level2: [],
        level3: [],
        level4: [],
        level5: [],
        coins : coins,
        title: '',
        currency: "",
        category: "",
        category_id: "",
        format: "",
        standard_days: "",
        premium_days: "",
        standard_price: "",
        premium_price: "",
        fast_days: "",
        fast_price: "",
        desc: "",
        gallery:[]
      };
  
  }

  handleFeature = (i, e) => {
   
    var name =  e.target.name;
    var temp = this.state.features;
    if(name == 'feature'){
      var val =  e.target.value;
      temp[i][name] = val;
    }else{
      var val =  e.target.checked;
      temp[i][name] = val ? 1 : 0;
    }
    console.log(temp);
    this.setState({
        features : temp
    })
  };

  addFeature = () => {
    var temp = this.state.features;
    temp.push({feature: '', standard: 0, premium : 1});
    this.setState({
      features: temp
    })
  }

  removeFeature = (i) => {
    var temp = this.state.features;
    delete temp[i];
    this.setState({
      features: temp
    })
  }

  handleExtra = (i, e) => {
   
    var name =  e.target.name;
    var temp = this.state.extras;
    var val =  e.target.value;
    temp[i][name] = val;
    console.log(temp);
    this.setState({
        extras : temp
    })
  };

  addExtra = () => {
    var temp = this.state.extras;
    temp.push({feature: '', value: '', amount : ''});
    this.setState({
      extras: temp
    })
  }

  removeExtra = (i) => {
    var temp = this.state.extras;
    delete temp[i];
    this.setState({
      extras: temp
    })
  }


  componentDidMount() {
   this.getGigCategories(1);
   getGig({id: this.props.match.params.id}).then(resp=>{
    this.setState({
      id: resp._id,
        gigs: resp,
        title: resp.subject,
        format: resp.format,
        currency: resp.preferedCurrency,
        desc: resp.text,
        extras: resp.extras,
        standard_days: resp.standardDays,
        premium_days: resp.premiumDays,
        standard_price: resp.standardPrice,
        premium_price: resp.premiumPrice,
        fast_days: resp.fastDays,
        fast_price: resp.fastPrice,
        features: resp.features,
        files: resp.contents
    })
   }, err=>{
       console.log(err);
   });
  }

  

  getGigCategories = (level) => {
    getCategories({level: level, parent: this.state.parent}).then(resp => {
      this.setState({
        ["level"+level] : resp
      })
    }, err => {
      console.log(err);
    })
  }
  

  handleInput = (e) => {
    var val =  e.target.value;
    var name =  e.target.name;
    if(name == 'title'){
      console.log(val.length);
      if(val.length > 100){
        console.log("over")
        val = val.substring(0, 100);
        console.log(this.state.title);
        console.log(val);
      }
      this.setState({
          [name] : val
      })
    }else{
      this.setState({
        [name] : val
      })
    }
  };

  handleCategory = (e, i) => {
      var val =  e.target.value;
      var v = val.split("^");
      this.setState({
        category: v[1],
        category_id: v[0],
        parent: v[0],
        level:i
      }, () => {
        if(i < 6){
        this.getGigCategories(i);
        }
      });
    
  };


  handleFile = (name, type, value) => {
    console.log(value);
    this.setState({
        [name] : value
    },()=>{
      var reader  = new FileReader();
    // it's onload event and you forgot (parameters)
      reader.onload = function(e)  {
          var image = document.getElementById(name+"_preview");
          image.src = e.target.result;
      }
      // you have to declare the file loading
      reader.readAsDataURL(value);
    })
  };
  

  submit = async (e) => {
    e.preventDefault();
    var err = [];
    if(this.state.title == ''){
      err.push("Title is required");
    }
    if(this.state.category == ''){
      err.push("Category is required");
    }
    if(this.state.files.length == 0){
      err.push("At least one gallery image is required");
    }
    if(err.length > 0){
      alertBox(true, err.join(", "));
    }
    else {
      var f = [];
      this.state.files.forEach(e=> {
        if(e.content_url == undefined){
          f.push(e);
        }
      })
      
      var formData = {
        id: this.state.id,
        subject: this.state.title,
        category: this.state.category,
        category_id: this.state.category_id,
        format: this.state.format,
        currency: this.state.currency,
        standard_price: this.state.standard_price,
        premium_price: this.state.premium_price,
        standard_days: this.state.standard_days,
        premium_days: this.state.premium_days,
        features: JSON.stringify(this.state.features),
        extras: JSON.stringify(this.state.extras),
        fast_days: this.state.fast_days,
        fast_price: this.state.fast_price,
        text: this.state.desc,
        files: f
      };
      console.log(formData);
      // const formData = new FormData();    
      // formData.append('postImgs', postImages);
      // formData.append('text', data);
      // formData.append('editorContent', JSON.stringify(edata));
      // formData.append('subject', this.state.title);
      // formData.append('hashtags', hashtags);
      // formData.append('paymentType', this.state.paymentType);
      // formData.append('amount', this.state.amount);
      // formData.append('currency', this.state.currency);
      // formData.append('category', this.state.category);
      switchLoader('Your post is being updated...');
      updateGig(formData)
      .then(async resp => {
        window.location.href="/passionomy";
        switchLoader();
      }, error => {
        alertBox(true, error.message);
        switchLoader();
      });
    }
  }
  

  handleDragEnter = e => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({
      dragclass: 'enter'
    })
  };
  handleDragLeave = e => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({
      dragclass: ''
    })
  };
  handleDragOver = e => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({
      dragclass: 'over'
    })
  };
  handleDrop = e => {
    e.preventDefault();
    e.stopPropagation();
    this.setState({
      dragclass: ''
    })
    let dt = e.dataTransfer
    let files = dt.files
    this.handleFiles(files);
  };
  
  handleClick = (e) => {
    e.preventDefault();
    e.stopPropagation();
    document.getElementById('gallery').click();
  };

  fileToDataURL = (file) => {
      console.log(file);
      if(file.content_url != undefined){
        return file;
      }else{
    var reader = new FileReader()
    return new Promise(function (resolve, reject) {
      reader.onload = function (event) {
        resolve(event.target.result)
      }
      reader.readAsDataURL(file)
    })
}
  }  
  
  readAsDataURL = (filesArray) => {
    // target => <input type="file" id="file">
    // console.log(target);
    // var filesArray = Array.prototype.slice.call(target.files)      
    return Promise.all(filesArray.map(this.fileToDataURL))
  }
  handleFiles = async (files) => {
    var temp = [...this.state.files, ...files];
    console.log(temp);
    var f = await this.readAsDataURL(temp);
    console.log(f);
    this.setState({
      files:  temp,
      imgs: f
    })
  }

  removeFile = (i) => {
    var temp = this.state.files;
    var temp2 = this.state.imgs;
    try {
        delete temp[i];
        delete temp2[i];    
    } catch (error) {
        
    }
    
    this.setState({
      files:temp,
      imgs:temp2
    })
  }

render() {
        return (
            // <!-- Wall container -->
            <div className="AddGigPage">
            <div className="container my-wall-container ">
              <div className="row mt-2">
                {
                    this.state.gigs == null ? 
                    <div className="col-sm empty-container-with-border center-column big mx-auto p-4"><Spinner /></div> : <div className="col-sm empty-container-with-border center-column big mx-auto p-4">
                  
                    <form onSubmit={(e) => this.submit(e)} method="post">
                        <div className="add-group">
                            <div className="add-row">
                                <div className="left">
                                <strong>Gig Title</strong>
                                </div>
                                <div className="right">
                                  <textarea type="text" placeholder="I will help you for..." name="title" className="form-control w-100" onChange={this.handleInput} value={this.state.title} required>{this.state.title}</textarea>
                                  <span className="add-comment">
                                      {this.state.title.length} / 100 max
                                  </span>
                                </div>
                            </div>
                        </div>
                        <div className="add-group">
                            <div className="add-row">
                                <div className="left">
                                <strong>Category</strong>
                                </div>
                                <div className="right">
                                  <select type="text" placeholder="I will help you for..." name="category" className="form-control w-100" onChange={(e) => this.handleCategory(e, 2)} >
                                    <option value="">Select</option>
                                    {
                                      this.state.level1.map((c, k) => {
                                        return <option key={k} value={c._id+"^"+c.category}>{c.category}</option>
                                      })
                                    }
                                  </select>
                                  {
                                    this.state.level2.length > 0 && <select type="text" placeholder="I will help you for..." name="category" className="form-control w-100" onChange={(e) => this.handleCategory(e, 3)} >
                                    <option value="">Select</option>
                                    {
                                      this.state.level2.map((c, k) => {
                                        return <option key={k} value={c._id+"^"+c.category}>{c.category}</option>
                                      })
                                    }
                                  </select>
                                  }
                                  {
                                    this.state.level3.length > 0 && <select type="text" placeholder="I will help you for..." name="category" className="form-control w-100" onChange={(e) => this.handleCategory(e, 4)} >
                                    <option value="">Select</option>
                                    {
                                      this.state.level3.map((c, k) => {
                                        return <option key={k} value={c._id+"^"+c.category}>{c.category}</option>
                                      })
                                    }
                                  </select>
                                  }
                                  {
                                    this.state.level4.length > 0 && <select type="text" placeholder="I will help you for..." name="category" className="form-control w-100" onChange={(e) => this.handleCategory(e, 5)} >
                                    <option value="">Select</option>
                                    {
                                      this.state.level4.map((c, k) => {
                                        return <option key={k} value={c._id+"^"+c.category}>{c.category}</option>
                                      })
                                    }
                                  </select>
                                  }
                                  {
                                    this.state.level5.length > 0 && <select type="text" placeholder="I will help you for..." name="category" className="form-control w-100" onChange={(e) => this.handleCategory(e, 6)} >
                                    <option value="">Select</option>
                                    {
                                      this.state.level5.map((c, k) => {
                                        return <option key={k} value={c._id+"^"+c.category}>{c.category}</option>
                                      })
                                    }
                                  </select>
                                  }
                                  
                                </div>
                            </div>
                        </div>
  
                        <div className="add-group">
                            <div className="add-row">
                                <div className="left">
                                <strong>File Format</strong>
                                </div>
                                <div className="right">
                                  <input type="text" required value={this.state.format} placeholder="Enter all file formats you will be sending" name="format" className="form-control w-100" onChange={this.handleInput} />
                                </div>
                            </div>
                        </div>
  
                        <div className="add-group">
                            <div className="add-row">
                                <div className="left">
                                <strong>Preferred Currency</strong>
                                </div>
                                <div className="right">
                                  <select name="currency" required className="form-control w-100" onChange={this.handleInput} value={this.state.currency} >
                                    <option value="">Select</option>
                                    {
                                      this.state.coins.map((e, i) => {
                                        return <option value={e.currencySymbol}>{e.currencyName+" ("+e.currencySymbol+")"}</option>
                                      })
                                    }
                                  </select>
                                </div>
                            </div>
                        </div>
  
                        <div className="add-group">
                            <div className="add-row">
                                <div className="left">
                                <strong>Pricing</strong>
                                </div>
                                <div className="right">
                                  <table className="table table-bordered">
                                    <tr><th></th><th>Standard</th><th>Premium</th></tr>
                                    <tr><td>Price</td><td><span className="input-icon">{this.state.currency}</span><input type="number" required placeholder="" step="1" min = "1" name="standard_price" className="form-control w-100" onChange={this.handleInput} required value={this.state.standard_price} /></td><td><span className="input-icon">{this.state.currency}</span> <input type="number" required placeholder="" step="1" min = "1" name="premium_price" className="form-control w-100" onChange={this.handleInput} value={this.state.premium_price} required /></td></tr>
                                    <tr><td>Delivery Time</td><td><input type="number" required placeholder="in days" step="1" min = "1" name="standard_days" className="form-control w-100" onChange={this.handleInput} value={this.state.standard_days} required /></td><td><input type="number" required placeholder="in days" step="1" min = "1" name="premium_days" className="form-control w-100" onChange={this.handleInput} value={this.state.premium_days} required /></td></tr>
                                    {
                                      this.state.features.map((v,i) => {
                                        return <tr key={i}><td><input type="text" placeholder="Add a Feature" required name="feature" className="form-control" value={this.state.features[i].feature} onChange={(e) => this.handleFeature(i, e)} /></td><td><input type="checkbox" name="standard" value="1" checked={this.state.features[i].standard == 0 ? false : true} className="checkbox d-block mx-auto" onChange={(e) => this.handleFeature(i, e)} /></td><td><input type="checkbox" name="premium" value="1" className="checkbox d-block mx-auto" onChange={(e) => this.handleFeature(i, e)} checked readOnly />
                                        {
                                          i > 0 && <span className="removeRow" onClick={() => this.removeFeature(i)}><i className="fa fa-times"></i></span>
                                        }
                                        </td></tr>
                                      })
                                    }
                                  </table>
                                  <Button type="button" onClick={this.addFeature} size="compact" variant="primary-outline">
                                    <i className="fa fa-plus"></i>
                                  </Button>
                                </div>
                            </div>
                        </div>
  
                        <div className="add-group">
                            <div className="add-row">
                                <div className="left">
                                <strong>Extras</strong>
                                </div>
                                <div className="right">
                                  <table className="table table-bordered">
                                    <tr><td>Fast Delivery</td><td><input type="number" required placeholder="in days" step="1" min = "1" name="fast_days" className="form-control w-100" onChange={this.handleInput} value={this.state.fast_days} /></td><td><span className="input-icon">{this.state.currency}</span> <input type="number" required placeholder="" step="1" min = "1" name="fast_price" className="form-control w-100" onChange={this.handleInput} value={this.state.fast_price} /></td></tr>
                                    {
                                      this.state.extras.map((v,i) => {
                                        return <tr key={i}><td><input type="text" placeholder="Add Extra Feature" required name="feature" className="form-control" onChange={(e) => this.handleExtra(i, e)} value={this.state.extras[i].feature} /></td><td><input type="text" name="value" className="form-control" value={this.state.extras[i].value} onChange={(e)=>this.handleExtra(i, e)} placeholder="value (optional)" /></td><td><span className="input-icon">{this.state.currency}</span> <input type="number" required placeholder="" step="1" min = "1" name="amount" className="form-control w-100" onChange={(e) => this.handleExtra(i, e)} value={this.state.extras[i].amount} />
                                        {
                                          i > 0 && <span className="removeRow" onClick={() => this.removeExtra(i)}><i className="fa fa-times"></i></span>
                                        }
                                        </td></tr>
                                      })
                                    }
                                  </table>
                                  <Button type="button" onClick={this.addExtra} size="compact" variant="primary-outline">
                                    <i className="fa fa-plus"></i>
                                  </Button>
                                </div>
                            </div>
                        </div>
  
                        <div className="add-group">
                            <div className="add-row">
                                <div className="left">
                                <strong>About</strong>
                                </div>
                                <div className="right">
                                  <textarea type="text" required placeholder="Description about this gig" name="desc" className="form-control w-100" onChange={this.handleInput} >{this.state.desc}</textarea>
                                </div>
                            </div>
                        </div>
  
                        <div className="add-group">
                            <div className="add-row">
                                <div className="left">
                                <strong>Gallery / My Work</strong>
                                </div>
                                <div className="right">
                                  <div className={"uploadArea "+this.state.dragclass} onDrop={e => this.handleDrop(e)}
                                    onDragOver={e => this.handleDragOver(e)}
                                    onDragEnter={e => this.handleDragEnter(e)}
                                    onDragLeave={e => this.handleDragLeave(e)}>
                                      <div className="droppedFiles">
                                      {
                                        this.state.files.map((f, i)=>{
                                          return <div className="file">
                                            <img src={f.content_url !=undefined ? f.content_url : this.state.imgs[i]} />
                                            <i className="fa fa-times" onClick={()=>this.removeFile(i)}></i>
                                          </div>
                                        })
                                      }
                                    </div>
                                    Drag and Drop
                                    
                                    {/* <input type="file" id="gallery" onChange={this.addToGallery} style={{display: "none"}} /> */}
                                  </div>
                                  {/* <Button type="button" variant="primary" size="compact" >Upload</Button> */}
                                </div>
                            </div>
                        </div>
  
                              <div className="form-group text-center">
                                  <Button variant="primary" size="big" >Publish</Button>
                                  {/* <Button type="button" variant="primary-outline" className="ml-2" size="big" onClick={(e) => this.review(e)}>Review</Button> */}
                              </div>
  
                    </form>
                  </div>
                  
                }
                
              </div>
            </div>

        

            </div>

      )
    }
}

export default EditGig;
